.Graph1 {
    grid-column: 1;
    grid-row: 3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -webkit-box-align: end;
        -ms-flex-align: end;
            align-items: flex-end;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
  }
  
  .Graph2 {
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
    grid-column: 3;
    grid-row: 3;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -webkit-box-pack: end;
        -ms-flex-pack: end;
            justify-content: flex-end;
  } 
  
  .Graph1 > div {
    background-color: var(--barGraph1-color);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 40px;
    margin-bottom: 5px;
    font-size: 10px;
    text-align: center;
    padding: 5px;
    border: 1px solid rgba(10, 10, 10, 0.15);
    -webkit-box-shadow: rgba(0, 0, 0) 1px 2px 3px;
            box-shadow: rgba(0, 0, 0) 1px 2px 3px;
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
  }

  .Graph2 > div {
    background-color: var(--barGraph2-color);
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    height: 40px;
    margin-bottom: 5px;
    font-size: 10px;
    text-align: center;
    padding: 5px;
    border: 1px solid rgba(10, 10, 10, 0.15);
    -webkit-box-shadow: rgba(0, 0, 0) 1px 2px 3px;
            box-shadow: rgba(0, 0, 0) 1px 2px 3px;
    min-width: -webkit-max-content;
    min-width: -moz-max-content;
    min-width: max-content;
  }


.Graph1-bar:hover, .Graph2-bar:hover {
    -webkit-animation: Spin 0.5s;
            animation: Spin 0.5s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
  }
